.Button {
    background: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
}

.Button:first-of-type {
    margin-left: 0;
    padding-left: 0;
}

.Success {
    color: red !important;
}

.Button:disabled{
    color:#ccc !important;
    cursor:not-allowed ;
}
.Danger {
    color: #944317 !important;
}


