.Toolbar{
	height:56px;
	width:100%;
	position:fixed;
	top:0;
	left:0;
	background:#703B09;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack:justify;
	    justify-content:space-between;
	-ms-flex-align:center;
	    align-items:center;
	padding:border-box;
	z-index:90;
}

.Toolbar nav{
	height:100%;
}

.Logo{
	height:80%;
}

@media (max-width:499px){
	.DesktopOnly{
		display:none;
	}
}


